import { toURLSearchParams } from '@wix/sdk-runtime/rest-modules';
import { transformSDKTimestampToRESTTimestamp } from '@wix/sdk-runtime/transformations/timestamp';
import { transformRESTTimestampToSDKTimestamp } from '@wix/sdk-runtime/transformations/timestamp';
import { transformPaths } from '@wix/sdk-runtime/transformations/transform-paths';
import { resolveUrl } from '@wix/sdk-runtime/rest-modules';
import { ResolveUrlOpts } from '@wix/sdk-runtime/rest-modules';
import { RequestOptionsFactory } from '@wix/sdk-types';

function resolveWixCashierOnboardingAvailabilityV1OnboardingAvailabilityServiceUrl(
  opts: Omit<ResolveUrlOpts, 'domainToMappings'>
) {
  const domainToMappings = {
    'manage._base_domain_': [
      {
        srcPath: '/v1/onboarding-availability',
        destPath: '/v1/onboarding-availability',
      },
      {
        srcPath: '/ambassador/v1/onboarding-availability',
        destPath: '/v1/onboarding-availability',
      },
    ],
  };

  return resolveUrl(Object.assign(opts, { domainToMappings }));
}

const PACKAGE_NAME = '@wix/auto_sdk_payments_onboarding-availability';

/** Fetch current state of onboarding availability for meta site. */
export function getOnboardingAvailability(
  payload: object
): RequestOptionsFactory<any> {
  function __getOnboardingAvailability({ host }: any) {
    const metadata = {
      entityFqdn:
        'wix.cashier.onboarding_availability.v1.onboarding_availability',
      method: 'GET' as any,
      methodFqn:
        'wix.cashier.onboarding_availability.v1.OnboardingAvailabilityService.GetOnboardingAvailability',
      packageName: PACKAGE_NAME,
      url: resolveWixCashierOnboardingAvailabilityV1OnboardingAvailabilityServiceUrl(
        { protoPath: '/v1/onboarding-availability', data: payload, host }
      ),
      params: toURLSearchParams(payload),
      transformResponse: (payload: any) =>
        transformPaths(payload, [
          {
            transformFn: transformRESTTimestampToSDKTimestamp,
            paths: [
              {
                path: 'onboardingAvailability.cbdFlow.attestationInfo.attestationFormSignedDate',
              },
            ],
          },
        ]),
    };

    return metadata;
  }

  return __getOnboardingAvailability;
}

/** Update current state of CBD flow for meta site. */
export function updateCbdFlow(payload: object): RequestOptionsFactory<any> {
  function __updateCbdFlow({ host }: any) {
    const serializedData = transformPaths(payload, [
      {
        transformFn: transformSDKTimestampToRESTTimestamp,
        paths: [{ path: 'cbdFlow.attestationInfo.attestationFormSignedDate' }],
      },
    ]);
    const metadata = {
      entityFqdn:
        'wix.cashier.onboarding_availability.v1.onboarding_availability',
      method: 'PATCH' as any,
      methodFqn:
        'wix.cashier.onboarding_availability.v1.OnboardingAvailabilityService.UpdateCbdFlow',
      packageName: PACKAGE_NAME,
      url: resolveWixCashierOnboardingAvailabilityV1OnboardingAvailabilityServiceUrl(
        {
          protoPath: '/v1/onboarding-availability/cbd-flow',
          data: serializedData,
          host,
        }
      ),
      data: serializedData,
      transformResponse: (payload: any) =>
        transformPaths(payload, [
          {
            transformFn: transformRESTTimestampToSDKTimestamp,
            paths: [
              {
                path: 'onboardingAvailability.cbdFlow.attestationInfo.attestationFormSignedDate',
              },
            ],
          },
        ]),
    };

    return metadata;
  }

  return __updateCbdFlow;
}

